.ViewSection {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 450px;
}

.flash1 {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 99;
  background: #fbe5e5;
}

.viewImg_box {
  position: relative;
  width: 250px;
  height: 250px;
  border-radius: 20px;
  overflow: hidden;
  border: 4px solid #453c3c;
  margin-top: 20px;
}

.animal_Img {
  position: absolute;
  width: 250px;
  height: 250px;
}

.animal_Img>img {
  position: absolute;

}

.capture-box {
  position: relative;
  width: 250px;
  height: 250px;
}